/* UFCEloEngine.css - Optimized to prevent double scrollbars */

/* Base Styles */
.ufc-elo-engine {
  display: flex;
  justify-content: center;
  padding: 0 5%;
  color: #fff;
  overflow-x: hidden;
  min-height: 100vh;
  height: 100%;
  -webkit-overflow-scrolling: touch;
  position: relative;
}

.content-container {
  display: flex;
  gap: 2%;
  width: 100%;
  max-width: 1600px;
  padding: 2% 0;
  overflow: visible;
}

/* Top Fighters Section */
.top-fighters {
  width: 23%;
  min-width: 280px;
  position: sticky;
  top: 20px;
  height: auto;
  align-self: flex-start;
}

.top-fighters h2 {
  font-size: 1.4em;
  margin-bottom: 1.2em;
  color: #00bfff;
}

.top-fighters ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.top-fighters li {
  margin-bottom: 0.8em;
  line-height: 1.3;
  font-size: 0.95em;
}

.top-fighters a {
  color: #ba1717;
  font-weight: bold;
  text-decoration: none;
  transition: color 0.2s;
}

.top-fighters a:hover {
  color: #1e90ff;
}

/* Search Section */
.search-container {
  flex: 1;
  width: 75%;
  overflow: visible;
}

.search-input-wrapper {
  display: flex;
  margin-bottom: 1.2em;
}

.search-bar {
  flex: 1;
  padding: 0.8em;
  font-size: 1em;
  border: 1px solid #555;
  border-radius: 5px 0 0 5px;
  background: #333;
  color: #fff;
}

.search-button {
  padding: 0.8em 1.5em;
  background-color: #00bfff;
  color: #fff;
  border: none;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  transition: background 0.2s;
}

.search-button:hover {
  background-color: #1e90ff;
}

/* Fighter Details */
.fighter-details {
  margin-top: 1.5em;
  font-size: 1em;
  overflow: visible;
}

.elo-chart {
  height: 400px;
  margin: 1.5em 0;
  background-color: #222;
  padding: 1.5em;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

/* Fight History Table */
.fight-history {
  width: 100%;
  margin-top: 1.5em;
  font-size: 0.9em;
  table-layout: fixed;
  border-collapse: collapse;
}

.fight-history th, 
.fight-history td {
  padding: 0.6em;
  border: 1px solid #444;
  word-break: break-word;
}

.fight-history th {
  background-color: #333;
  color: #00bfff;
  font-weight: 600;
}

.fight-history td {
  background-color: #2c2c2c;
}

/* Suggestions Dropdown */
.suggestions ul {
  list-style-type: none;
  padding: 0;
  margin: 0.5em 0 0;
  background-color: #333;
  border: 1px solid #555;
  border-radius: 4px;
}

.suggestions li {
  padding: 0.8em;
  cursor: pointer;
  transition: background 0.2s;
}

.suggestions li:hover {
  background-color: #444;
}

/* Mobile Responsiveness */
@media (max-width: 1200px) {
  .content-container {
    flex-direction: column;
    gap: 20px;
  }
  
  .top-fighters {
    position: relative;
    width: 100%;
    top: auto;
    margin-bottom: 2em;
  }
  
  .search-container {
    width: 100%;
  }
}

@media (max-width: 768px) {
  .ufc-elo-engine {
    padding: 0 3%;
    height: auto;
    overflow: hidden;
  }
  
  .search-input-wrapper {
    flex-direction: column;
  }
  
  .search-bar {
    border-radius: 5px;
    margin-bottom: 0.5em;
  }
  
  .search-button {
    border-radius: 5px;
    width: 100%;
  }
  
  .elo-chart {
    height: 300px;
    padding: 1em;
  }
  
  /* Simplify fight history for mobile */
  .fight-history th:nth-child(n+4),
  .fight-history td:nth-child(n+4) {
    display: none;
  }
}

/* Ultra-wide screens */
@media (min-width: 1600px) {
  .content-container {
    max-width: 1800px;
  }
}

